<template>
	<div id="statcenter-current">
		<public-header title="订单来源详情"></public-header>
		<div class="content padding-15 font-14 ">
			<van-row class="c-gray">
				<van-col span="5" class="padding-10-tb">账期</van-col>
				<van-col span="3" class="padding-10-tb">总单数</van-col>
				<van-col span="5" class="padding-10-tb">饿了么</van-col>
				<van-col span="5" class="padding-10-tb">美团</van-col>
				<van-col span="5" class="padding-10-tb">小程序</van-col>
				<van-col span="5" class="padding-10-tb"></van-col>
				<van-col span="3" class="padding-10-tb"></van-col>
				<van-col span="5" class="padding-10-b">订单/占比</van-col>
				<van-col span="5" class="padding-10-b">订单/占比</van-col>
				<van-col span="5" class="padding-10-b">订单/占比</van-col>
				<van-col span="5" class="padding-10-tb"></van-col>
				<van-col span="3" class="padding-10-tb"></van-col>
				<van-col span="5" class="padding-10-tb">H5</van-col>
				<van-col span="5" class="padding-10-tb">顾客APP</van-col>
				<van-col span="5" class="padding-10-tb">后台创建</van-col>
				<van-col span="5" class="padding-10-tb"></van-col>
				<van-col span="3" class="padding-10-tb"></van-col>
				<van-col span="5" class="padding-10-b">订单/占比</van-col>
				<van-col span="5" class="padding-10-b">订单/占比</van-col>
				<van-col span="5" class="padding-10-b">订单/占比</van-col>
			</van-row>
			<van-row v-if='0'>
				<van-col span="6">总计</van-col>
				<van-col span="5">{{stat.total_success_order}}</van-col>
				<van-col span="5">{{stat.total_fee}}</van-col>
				<van-col span="5">{{stat.total_final_fee}}</van-col>
				<van-col span="3">{{stat.total_refund_fee}}</van-col>
			</van-row>
			<van-row v-for="(item, index) in detail" :key="index" v-if="detail">
				<van-col span="5">{{item.stat_day}}</van-col>
				<van-col span="3">{{item.total_success_order}}</van-col>
				<van-col span="5">{{item.order_eleme + '/' + item.pre_order_eleme + '%'}}</van-col>
				<van-col span="5">{{item.order_meituan + '/' + item.pre_order_meituan + '%'}}</van-col>
				<van-col span="5">{{item.order_wxapp + '/' + item.pre_order_wxapp + '%'}}</van-col>
				<van-col span="5"></van-col>
				<van-col span="3"></van-col>
				<van-col span="5">{{item.order_wap + '/' + item.pre_order_wap + '%'}}</van-col>
				<van-col span="5">{{item.order_h5app + '/' + item.pre_order_h5app + '%'}}</van-col>
				<van-col span="5">{{item.order_plateformCreate + '/' + item.pre_order_plateformCreate + '%'}}</van-col>
			</van-row>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import {mapState} from 'vuex'
	import publicHeader from "@/components/header"
	import {getData} from "../../controller/funcCommon.js"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				stat: {},
				detail: []
			}
		},
		methods: {
			onLoad() {
				let that = this;
				getData({
					vue: this,
					data: {
						detail: 1,
						filter: that.filter ? that.filter.items: {}
					},
					url: 'plateform/statcenter/takeoutOrderChannel/index',
					success: function(result) {
						that.detail = result.detail;
						that.stat = result.stat;
					}
				});
			}
		},
		computed: {
			...mapState([
				'filter',
			]),
		},
		mounted() {
			this.onLoad();
		},

	}
</script>

<style scoped>
	#statcenter-current .van-row{
		font-size: 12px;
		text-align: center;
	}
	#statcenter-current .van-row:nth-child(odd) {
		background-color: #ddd;
	}
	#statcenter-current .van-row .van-col{
		padding: 8px 0;
		border-right: 1px solid #eee;
	}
	#statcenter-current .van-row .van-col:last-child{
		border-right: 0px;
	}
</style>


